ul.Menu {
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;

    font-size: 16pt;
    font-family: "Ubuntu";
    width: 268px;

}

li.MenuLi {

    width: 268px;
    height: 60px;

}

li.MenuLi a {
    display: block;
    color: black;
    border-radius: 10px;
    text-align: left;
    padding: 14px 16px 16px 16px;
    text-decoration: none;
}

li.MenuLi a:hover {

}

.active_item {
    background: #F3F1ED;
}
.logo{

    width: 100px;
    height: 36px;
    color: black;
    font-size: 16pt ;
    text-align: left;
    font-family: 'Zilla Slab', serif;
}
.logo-img{
    margin-left: 100px;

    width: 36px;
    height: 36px;
}
.search-box{
    width: 500px;
    background: #F3F1ED;
    border-top-left-radius: 20px;
    border-bottom-left-radius: 20px ;
}

.search-box-btn{
    position: relative;
    background: #F3F1ED;
    top: -3px;
    right: 3px;
    border-top-right-radius: 20px;
    border-bottom-right-radius: 20px ;
}
.magnifier{
    position: relative;
    left: 20px;
    z-index: 2;
    width: 16px;
    height: 16px;
    color: #666666;
}
.search-box-placeholder{
    position: relative;
    z-index: 2;
    width: 106px;
    height: 18px;
    left: 20px;
    bottom:28px;
    font-family: Ubuntu;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 18px;

    color: #666666;
}
.vector{
    position: relative;
    left: 200px;
    width: 17px;
    height: 17px;

}



ul.Navigation {
    position: absolute;
    top: 5px;
    list-style-type: none;
    padding: 0;
    overflow: hidden;
    right: 20px;
    width: 100px;
    font-family: "Ubuntu";
}

li.NavigationLi {
    float: left;
}

li.NavigationLi a {
    display: block;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
}

li.NavigationLi a:hover {
    border-radius: 10px;
    background: #F3F1ED;
}

